<template>
	<v-container>
		<loading v-if="!currentRace" />
		<div v-else>
			<v-overlay :absolute="absolute" :value="loading" v-if="loading">
				<loading />
			</v-overlay>
			<div>
				<v-row
					class="fill-height"
					align-content="center"
					justify="center"
				>
					<v-col md="8" sm="12">
						<v-text-field label="WORD" v-model="word" @keypress.enter="addSelection">
							<template slot="append">
								<tooltip text="Create quiz">
									<v-btn
										icon
										:disabled="!word"
										@click="addSelection"
									>
										<v-icon>mdi-plus</v-icon>
									</v-btn>
								</tooltip>
								<tooltip text="Create quiz and open">
									<v-btn
										icon
										:disabled="!word"
										@click="addAndMove2Selection"
									>
										<v-icon>mdi-open-in-new</v-icon>
									</v-btn>
								</tooltip>
							</template>
						</v-text-field>
					</v-col>
				</v-row>
				<v-row dense>
					<v-col
						v-for="(selection, idx) in currentRaceSorted"
						:key="idx"
					>
						<selection-card
							:selection="selection"
							:index="+selection.index"
						/>
					</v-col>
				</v-row>
				<!-- <add-card @click="addSelection" /> -->
			</div>
		</div>
	</v-container>
</template>

<script>
import Loading from "@/common/components/Loading.vue";
import { mapState, mapActions } from "vuex";
import AddCard from "../components/AddCard.vue";
import SelectionCard from "../components/SelectionCard.vue";
import { createSelection } from "../services/firebaseServices";
import Tooltip from "@/common/components/Tooltip.vue";
export default {
	components: { SelectionCard, AddCard, Loading, Tooltip },
	computed: {
		...mapState("user", ["currentRace"]),
		currentRaceSorted() {
			if (Array.isArray(this.currentRace)) {
				return (
					this.currentRace &&
					this.currentRace
						.map((v, index) => {
							return { ...v, index };
						})
						.sort(
							(a, b) =>
								Boolean(a.selected) - +Boolean(b.selected) ||
								+Object.entries(b.pictures).length -
									+Object.entries(a.pictures).length
						)
				);
			}
		},
	},
	data() {
		return {
			word: "",
			loading: false,
		};
	},
	methods: {
		async addSelection() {
			if (!this.word) {
				this.$toast.error("Word must not be void");
			}
			try {
				await createSelection(this.word);
				this.$toast.success(`Added ${this.word} quest`);
				this.word = "";
				return true;
			} catch (e) {
				this.$toast.error(e.message);
				return false;
			}
		},
		async addAndMove2Selection() {
			this.loading = true;
			if (await this.addSelection()) {
				this.$router.push("/user/selection/" + this.currentRace.length);
			}
			this.loading = false;
		},
	},
};
</script>

