<template>
	<v-card
		min-width="300px"
		max-width="300px"
		:class="'all-card ' + (done ? 'selected-card' : 'selection-card')"
		@click="openSelection"
		elevation="5"
	>
		<v-card-title style="padding: 20px">
			<v-row>
				<v-icon v-if="selection.selected" outlined color="green"
					>mdi-check</v-icon
				>
				<div>{{ selection.word }}</div>
				<v-spacer />
				<v-badge
					:color="pictsLength > 2 ? 'green' : 'red'"
					:content="+pictsLength || '0'"
					v-if="!selection.selected"
				>
					<v-icon> mdi-image </v-icon>
				</v-badge>
				<div class="winner-info" v-else>
					by {{ selection.selected.user }}
				</div>
			</v-row>
		</v-card-title>
		<v-card-text>
			<v-img
				v-if="selection.selected"
				:src="selection.selected.thumbnail"
				:lazy-src="selection.selected.thumbnail"
				contain
				max-height="100px"
				min-height="100px"
			>
				<template slot="placeholder">
					<img-placeholder />
				</template>
			</v-img>
			<div v-else-if="pictsLength">
				<v-carousel
					hide-delimiters
					show-arrows-on-hover
					cycle
					:interval="slideInterval"
					hide-delimiter-background
					height="100px"
				>
					<div
						v-for="(pict, index) in selection.pictures"
						:key="index"
						:text="pict.title"
					>
						<v-carousel-item>
							<v-row
								class="fill-height"
								align="center"
								justify="center"
							>
								<v-img
									class="my-3"
									contain
									:src="pict.thumbnail"
									:lazy-src="pict.thumbnail"
									height="100%"
								>
									<template slot="placeholder">
										<img-placeholder />
									</template>
								</v-img>
							</v-row>
						</v-carousel-item>
					</div>
				</v-carousel>
			</div>
			<h4 v-else>Awaiting proposals...</h4>
		</v-card-text>
	</v-card>
</template>

<script>
import ImgPlaceholder from '@/common/components/ImgPlaceholder.vue';
export default {
	components: { ImgPlaceholder },
	props: {
		selection: Object,
		index: { type: Number, requied: true },
	},
	methods: {
		openSelection() {
			this.$router.push("/user/selection/" + (this.index + 1));
		},
	},
	computed: {
		pictsLength() {
			return Object.entries(this.selection.pictures).length;
		},
		slideInterval() {
			return 1300 + Math.random() * 400;
		},
		done() {
			return Boolean(this.selection.selected);
		},
	},
};
</script>

<style>
.all-card {
	margin: 20px;
}
.selection-card {
	cursor: pointer;
}
.selection-card:hover {
	background-color: gainsboro !important;
}
.winner-info {
	color: green;
	font-size: small;
	font-weight: bold;
}
</style>
